document.addEventListener('DOMContentLoaded', function(){
    eventoDeTexto();
})
let mostrarContenido = false;
function eventoDeTexto(){
    const body = document.querySelector('body');
    const click = document.querySelector('.img-1');
    click.addEventListener('click', () => {
        
        if (!mostrarContenido) {
            generarTextoCap();
            mostrarContenido = true;
            
        }else{
            const borrar = document.getElementById('img-id');
            while(borrar.firstChild){
                borrar.removeChild(borrar.firstChild);
                body.classList.remove('body-Color')
                click.classList.remove('opacar');
                mostrarContenido = false;
            }
        }
    });
}
function generarTextoCap(){
    const body = document.querySelector('body');
    body.classList.add('body-Color')
    const mostrarDiv = document.querySelector('.img-1');
    mostrarDiv.classList.add('opacar')
    mostrarDiv.classList.add('mostrar')
    const div = document.createElement('DIV')
    div.classList.add('div-size')
    const TituloCap = document.createElement('H5');
    TituloCap.textContent = 'Capilares';
    const parrafo = document.createElement('P');
    parrafo.textContent = "laudantium dolores iure, sint quibusdam ex hic officiis reprehenderit ducimus nam aut rem corrupti necessitatibusLorem ipsum dolor sit amet consectetursaepe quos, laudantium dolores iure, sint quibusdam ex hic officiis reprehenderit ducimus facilis quam sunt vero nam aut rem corrupti necessitatibus."
    div.appendChild(TituloCap)
    div.appendChild(parrafo)
    mostrarDiv.appendChild(div)
        
        
}